/* eslint indent: ["off"] */
import $ from 'dom7';
export default {
  render: function render(self) {
    return "\n      <div class=\"color-picker-module color-picker-module-palette\">\n        <div class=\"color-picker-palette\">\n          ".concat(self.params.palette.map(function (p) {
      if (Array.isArray(p)) {
        var row = '<div class="color-picker-palette-row">';
        row += p.map(function (c) {
          return "\n                <div class=\"color-picker-palette-value\" data-palette-color=\"".concat(c, "\" style=\"background-color: ").concat(c, "\"></div>\n              ");
        }).join('');
        row += '</div>';
        return row;
      }

      return "\n              <div class=\"color-picker-palette-value\" data-palette-color=\"".concat(p, "\" style=\"background-color: ").concat(p, "\"></div>\n            ");
    }).join(''), "\n        </div>\n      </div>\n    ");
  },
  init: function init(self) {
    function handlePaletteClick(e) {
      var hex = $(e.target).attr('data-palette-color');
      self.setValue({
        hex: hex
      });
    }

    self.$el.on('click', '.color-picker-module-palette .color-picker-palette-value', handlePaletteClick);

    self.destroyPaletteEvents = function destroyPaletteEvents() {
      self.$el.off('click', '.color-picker-module-hex input', handlePaletteClick);
    };
  },
  destroy: function destroy(self) {
    if (self.destroyPaletteEvents) {
      self.destroyPaletteEvents();
    }

    delete self.destroyPaletteEvents;
  }
};